import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { compose } from "redux";
import { firebaseConnect } from "react-redux-firebase";

import DashboardNav from "../layout/DashboardNav";

class UserProfile extends Component {
  state = {
    displayName: "",
    email: "",
    accountNumber: "",
    bankName: "",
    phoneNumber: "",
    redirect: false
  };

  handleSubmit = e => {
    e.preventDefault();
    // Code snippet to handle submit event
    const {
      displayName,
      email,
      accountNumber,
      bankName,
      phoneNumber
    } = this.state;

    const { firebase } = this.props;
    const user = firebase.auth().currentUser;

    user
      .updateProfile({
        displayName,
        email,
        phoneNumber,
        bankName,
        accountNumber
      })
      .then(() => this.setRedirect())
      .catch(err => console.log(err.stack));
  };

  handleChange = e => {
    e.preventDefault();
    // Code snippet to handle change event
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  setRedirect(){
    this.setState({redirect: true})
  };

  renderRedirect(){
    if (this.state.redirect){
      return <Redirect to="/dashboard" />
    }
  }
  render() {
    const { auth } = this.props;

    return (
      <div>
        {this.renderRedirect()};
        <DashboardNav />
        <div className="container">
          <p className="text-primary small font-weight-bold mt-5">
            <Link to="/dashboard">Back to dashboard</Link>
          </p>

          <div class="row">
            <div class="col-lg-8 mb-4">
              <p className="h3 text-center">
                User's Profile <i className="fa fa-user"></i>
              </p>
              <form
                class=" border border-light p-2 text-left"
                onSubmit={this.handleSubmit}
              >
                <div className="form">
                  <div className="col col-sm-6 mb-4">
                    <input
                      type="text"
                      placeholder="Display Name"
                      className="form-control"
                      name="displayName"
                      value={this.state.displayName}
                      onChange={this.handleChange}
                    />
                  </div>
                  <div className="col col-sm-6 mb-4">
                    <input
                      type="email"
                      className="form-control"
                      name="email"
                      placeholder={auth.email}
                      value={this.state.email}
                      onChange={this.handleChange}
                    />
                  </div>
                  <div className="col col-sm-6 mb-4">
                    <input
                      type="text"
                      placeholder="Account number"
                      className="form-control"
                      name="accountNumber"
                      value={this.state.accountNumber}
                      onChange={this.handleChange}
                    />
                  </div>
                  <div className="col col-sm-6 mb-4">
                    <input
                      type="text"
                      placeholder="Phone no"
                      className="form-control"
                      name="phoneNumber"
                      value={this.state.phoneNumber}
                      onChange={this.handleChange}
                    />
                  </div>
                </div>
                <p className="text-primary small font-weight-bold ">
                  Select bank
                </p>
                <select className="browser-default col col-sm-6  custom-select mb-4 form-control">
                  <option value="2" selected>
                    Access Bank
                  </option>
                  <option value="3">UBA</option>
                  <option value="4">First Bank</option>
                  <option value="5">Zenith Bank</option>
                </select>

                <button
                  class="btn orange-gradient font-weight-bold text-left mt-5"
                  type="submit"
                >
                  Update
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

UserProfile.propTypes = {
  firebase: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired
};

export default compose(
  firebaseConnect(),
  connect((state, props) => ({
    auth: state.firebase.auth
  }))
)(UserProfile);
